import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    score: 0,
    health: 100,
  },
  mutations: {
    incrementScore(state, score) {
      state.score += score;
    },
    decrementHealth(state, damage) {
      state.health -= damage;
    },
  },
  actions: {
    incrementScore({ commit }, score) {
      commit('incrementScore', score);
    },
    decrementHealth({ commit, dispatch, state }, damage) {
      if (state.health - damage <= 0) {
        dispatch('playerDied');
      } else {
        commit('decrementHealth', damage);
      }
    },
    playerDied() {
      console.log('player died');
    },
  },
  modules: {
  },
});
