import AFRAME from 'aframe';
import store from '@/store';

AFRAME.registerComponent('enemy', {
  init() {
    this.el.addEventListener('stateadded', this.damagePlayer);
  },
  damagePlayer(event) {
    const el = event.target;

    if (el.is('collided')) {
      el.setAttribute('visible', 'false');
      store.dispatch('decrementHealth', 99);
    }
  },
  remove() {
    this.el.removeEventListener('stateadded', this.damagePlayer);
  },
});
